/**
 * LAYOUT - Toolkit containers and wrapper
 */

html {
  -ms-overflow-style: -ms-autohiding-scrollbar;
}

body {
	background: $color-module-dark; //gives the apperance of the page ending as the site container is white
}

.site-container {
	background: white;	
	margin: 0 auto;
}

[class^="site-container"] {
	margin: 0 auto;
	background: white;
	box-shadow: 0px 0px 2px 1px rgba(0,0,0,0.1);
}

.site-container-fw,
.site-container-lg { // full width container, used with the full width sidebar
	max-width: $site-container-lg;	
}

.site-container-sm {
	max-width: $site-container-sm;	
}

.site-container-md {
	max-width: $site-container-md;	
}

.jadu-cms, //deprecated
.cms { //used elsewhere throughtout the site where a wysiwyg in a cms would be present
    margin-bottom: $space-xl;
}

/**
 * Wrappers - Alternative to BS .container
 */

[class*="wrapper"] {
	@extend .clearfix;
	margin: 0 auto;
	
}

.wrapper-lg {	
	max-width: $wrapper-lg;
}

.wrapper-md {
    max-width: $wrapper-md;
}

.wrapper-sm {
    max-width: $wrapper-sm;
}

.wrapper-xs {
    max-width: $wrapper-xs;
}



/**



//Spacing
$space-xl: 						26px;
$space-lg: 						22px;
$space:                         16px;
$space-sm: 						12px;
$space-xs: 						10px;
$space-half:                    8px;


*/



.wrapper-padded, //depracated
.wrapper-pd {	
	padding: 0 $space-sm;
	@media (min-width: $screen-xs-min) {
		padding: 0 $space;
	}
}

.wrapper-pd-md {	// 30px
	padding: 0 $space;
	@media (min-width: $screen-md-min) {
		//padding: 0 $space*3;
		padding: 0 30px;
	}
}

.wrapper-pd-lg {	
	padding: 0 $space-lg;
	@media (min-width: $screen-md-min) {
		padding: 0 $space-lg*3;
	}
}

.wrapper-pd-sm {
	padding: 0 $space-sm;	
}

.wrapper-pd-xs {
	padding: 0 $space-xs;	
}

.wrapper-pd-xss {
	padding: 0 6px;	
}

/* Content row or widget rows */

.container-row {
	padding: $space-sm 0;	
	@media (min-width: $screen-xs-min) {
		padding: $space-lg 0;
	}
}

.container-header {
	padding: 20px 0;
}