.visible-on .col-xs-6 .visible-xs-block {
    color: #468847;
    background-color: #dff0d8;
    border: 1px solid #d6e9c6
}
.bs-glyphicons {
    margin: 0 -10px 20px;
    overflow: hidden
}
.bs-glyphicons-list {
    padding-left: 0;
    list-style: none
}
.bs-glyphicons li {
    float: left;
    width: 25%;
    height: 115px;
    padding: 10px;
    font-size: 10px;
    line-height: 1.4;
    text-align: center;
    background-color: #f9f9f9;
    border: 1px solid #fff
}
.bs-glyphicons .glyphicon {
    margin-top: 5px;
    margin-bottom: 10px;
    font-size: 24px
}
.bs-glyphicons .glyphicon-class {
    display: block;
    text-align: center;
    word-wrap: break-word
}
.bs-glyphicons li:hover {
    color: #fff;
    background-color: #563d7c
}
@media (min-width: 768px) {
    .bs-glyphicons {
        margin-right: 0;
        margin-left: 0
    }
    .bs-glyphicons li {
        width: 12.5%;
        font-size: 12px
    }
}
.bs-customizer .toggle {
    float: right;
    margin-top: 25px
}
.bs-customizer label {
    margin-top: 10px;
    font-weight: 500;
    color: #555
}
.bs-customizer h2 {
    padding-top: 30px;
    margin-top: 0;
    margin-bottom: 5px
}
.bs-customizer h3 {
    margin-bottom: 0
}
.bs-customizer h4 {
    margin-top: 15px;
    margin-bottom: 0
}
.bs-customizer .bs-callout h4 {
    margin-top: 0;
    margin-bottom: 5px
}
.bs-customizer input[type=text] {
    font-family: Menlo, Monaco, Consolas, "Courier New", monospace;
    background-color: #fafafa
}
.bs-customizer .help-block {
    margin-bottom: 5px;
    font-size: 12px
}
#less-section label {
    font-weight: 400
}
.bs-customize-download .btn-outline {
    padding: 20px
}
.bs-customizer-alert {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1030;
    padding: 15px 0;
    color: #fff;
    background-color: #d9534f;
    border-bottom: 1px solid #b94441;
    -webkit-box-shadow: inset 0 1px 0 rgba(255, 255, 255, .25);
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, .25)
}
.bs-customizer-alert .close {
    margin-top: -4px;
    font-size: 24px
}
.bs-customizer-alert p {
    margin-bottom: 0
}
.bs-customizer-alert .glyphicon {
    margin-right: 5px
}
.bs-customizer-alert pre {
    margin: 10px 0 0;
    color: #fff;
    background-color: #a83c3a;
    border-color: #973634;
    -webkit-box-shadow: inset 0 2px 4px rgba(0, 0, 0, .05), 0 1px 0 rgba(255, 255, 255, .1);
    box-shadow: inset 0 2px 4px rgba(0, 0, 0, .05), 0 1px 0 rgba(255, 255, 255, .1)
}
.bs-dropzone {
    position: relative;
    padding: 20px;
    margin-bottom: 20px;
    color: #777;
    text-align: center;
    border: 2px dashed #eee;
    border-radius: 4px
}
.bs-dropzone .import-header {
    margin-bottom: 5px
}
.bs-dropzone .glyphicon-download-alt {
    font-size: 40px
}
.bs-dropzone hr {
    width: 100px
}
.bs-dropzone .lead {
    margin-bottom: 10px;
    font-weight: 400;
    color: #333
}
#import-manual-trigger {
    cursor: pointer
}
.bs-dropzone p:last-child {
    margin-bottom: 0
}
.bs-brand-logos {
    display: table;
    width: 100%;
    margin-bottom: 15px;
    overflow: hidden;
    color: #563d7c;
    background-color: #f9f9f9;
    border-radius: 4px
}
.bs-brand-item {
    padding: 60px 0;
    text-align: center
}
.bs-brand-item+.bs-brand-item {
    border-top: 1px solid #fff
}
.bs-brand-logos .inverse {
    color: #fff;
    background-color: #563d7c
}
.bs-brand-item h1,
.bs-brand-item h3 {
    margin-top: 0;
    margin-bottom: 0
}
.bs-brand-item .bs-docs-booticon {
    margin-right: auto;
    margin-left: auto
}
.bs-brand-item .glyphicon {
    width: 30px;
    height: 30px;
    margin: 10px auto -10px;
    line-height: 30px;
    color: #fff;
    border-radius: 50%
}
.bs-brand-item .glyphicon-ok {
    background-color: #5cb85c
}
.bs-brand-item .glyphicon-remove {
    background-color: #d9534f
}
@media (min-width: 768px) {
    .bs-brand-item {
        display: table-cell;
        width: 1%
    }
    .bs-brand-item+.bs-brand-item {
        border-top: 0;
        border-left: 1px solid #fff
    }
    .bs-brand-item h1 {
        font-size: 60px
    }
}
.zero-clipboard {
    position: relative;
    display: none
}
.btn-clipboard {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 10;
    display: block;
    padding: 5px 8px;
    font-size: 12px;
    color: #767676;
    cursor: pointer;
    background-color: #fff;
    border: 1px solid #e1e1e8;
    border-radius: 0 4px 0 4px
}
.btn-clipboard-hover {
    color: #fff;
    background-color: #563d7c;
    border-color: #563d7c
}
@media (min-width: 768px) {
    .zero-clipboard {
        display: block
    }
    .bs-example+.zero-clipboard .btn-clipboard {
        top: -16px;
        border-top-right-radius: 0
    }
}
.anchorjs-link {
    color: inherit
}
@media (max-width: 480px) {
    .anchorjs-link {
        display: none
    }
}
:hover>.anchorjs-link {
    opacity: .75;
    -webkit-transition: color .16s linear;
    -o-transition: color .16s linear;
    transition: color .16s linear
}
.anchorjs-link:focus,
:hover>.anchorjs-link:hover {
    text-decoration: none;
    opacity: 1
}
#focusedInput {
    border-color: #ccc;
    border-color: rgba(82, 168, 236, .8);
    outline: 0;
    outline: thin dotted\9;
    -webkit-box-shadow: 0 0 8px rgba(82, 168, 236, .6);
    box-shadow: 0 0 8px rgba(82, 168, 236, .6)
}
