/**
 * Custopm toolkit docs styles
 */

.tk-docs {

    h1 {
        //border-bottom: 1px solid $color-border;
        //font-size: 38px;
    }

    pre {
        border-radius: 5px; 
    }

    code {
        white-space: pre;
        font-size: 13px;

        span {
            font-size: 13px !important;
        }
    }
}

//Code styles

.tk-example {
    border: 1px solid $color-border;
    border-bottom: 0;
    padding:  60px 15px 15px 15px;
    border-radius: 0;    
    position: relative;
    &:before {      
        content:"Example";
        text-transform: uppercase;
        color: $color-font-light;
        font-size: 14px;
        margin-bottom: 30px;
        letter-spacing: 1px;
        position: absolute;
        top: 15px;
        left: 15px;
    }
}

.tk-docs {

    // h1,h2,h3,h4 {
    //     border-bottom: 1px solid $color-border;
    // }

    // h1 {
    //     font-size: 36px;
    // }

    // h2 {
    //     font-size: 30px;   
    // }

    // h3 {
    //     font-size: 24px;   
    // }

    // h4 {
    //     font-size: 20px;   
    // }

    pre {
        margin: 0;
        border: 1px solid $color-border;
        //padding: 10px;
        border-radius: 5px;
        overflow: scroll !important;
        margin-bottom: 40px;
        box-shadow: none;   
        box-shadow: 0;
        position: relative;
        margin: 0px 0 30px 0;
        border-radius: 0;
        border-left: 3px solid $color-masthead;
        background-color: #fdfdfd;    
        background-image: linear-gradient(transparent 50%, $color-module-light 50%);
        background-size: 40px 40px;
        background-origin: content-box;
        overflow: visible;
        padding: 20px 0;        

        code {
            line-height: 1.6em;
        }
    }    

    .tk-example + pre {
        border-radius: 0;
        // margin: 0;
        // border: 0;
        // padding: 0;
        //border-radius: 0 0 5px 5px;
        // overflow: scroll;
        // margin-bottom: 40px;
        // box-shadow: none;    
    }

}

//tk docs nav

.nav-docs {
    padding: 20px;    

    ul {
        padding: 0 0 0 20px;
    }
    
    li {
        list-style: none;
    }

    a {
        font-size: 14px;
        border-left: 1px solid white;
        padding-left: 10px;
        color: $color-font;

        &:hover,
        &:focus {
            border-left: 1px solid $color-brand-2;
            text-decoration: none;
        }
    }

    .active {
        color: $color-brand-2;
        border-left: 1px solid $color-brand-2;
    }
}

.docs-nav-fixed .nav-docs {
    position: fixed;
    top: 0;    
    overflow: scroll;
    height: 100%;

}

