/**
 * HTML.html template
 */

.swatch {
    border: 1px solid $color-border;
    padding: $space-half/2;
    border-radius: $border-radius-base;
    width: 9%;
    min-width: 80px;
    float:left;
    margin:  0 0.5%  $space-half/2 0.5%;

    h4 {
        margin: 0;
        font-size: 12px;
        text-align: center;     
        font-family: $font-family-sans-serif;
    }
}

.swatch-box {
    width: 100%;
    padding-bottom: 100%;
    background: #eee;
    margin-bottom: $space-half/2;
}

.color-brand-1 {
    background: $color-brand-1;
}
.color-brand-1-dark {
    background: $color-brand-1-dark;
}
.color-brand-1-light {
