/**
 * Columns - Used for the page sidebar layout
 */

.column-container {
    @extend .clearfix;
    position: relative;

    .column-container-primary {
        padding-bottom: 10px;
        @media (min-width: $screen-sm-min) {
            float: left;
            width: 75%;        
            padding-bottom: 20px;                
        } 
        @media (min-width: $screen-md-min) {
            float: left;
            width: 75%;     
            padding-bottom: 40px;               
        }
    }

    .column-container-secondary {    
    
        position: relative;
        
        @media (min-width: $screen-sm-min) {
            float: left;
            width: 25%;        
            padding-bottom: 20px;
            padding-left: 10px;
        } 
        @media (min-width: $screen-md-min) {
            float: left;
            width: 25%;                
            padding-bottom: 40px;            
            max-width: 320px;                        
        }    
    }

    .column-container-secondary-right {    
        @media (min-width: $screen-sm-min) {
            float: right;
            width: 25%;        
            border-right: 0;        
        } 
        @media (min-width: $screen-md-min) {
            float: right;
            width: 25%;        
            border-right: 0;        
        }    
    }

}

//full width option

.column-container-fw {

    @media (min-width: $screen-md-min) {  
        display: table;
        width: 100%;
    }

    .column-container-primary {       
        @media (min-width: $screen-md-min) {     
            width: auto;                   
            padding-bottom: 40px;  
            display: table-cell;      
            float: none;                   
            max-width: 5px; //when the swiper is in this container max width stops it from overflowing
        }
    }

    .column-container-secondary {     

        padding-left: 0;           
           
        @media (min-width: $screen-md-min) {
            float: none;                                    
            display: table-cell;
            background: $color-module;   
            padding: 0 0 40px 0; 
            width: 250px;        
        }    

        @media (min-width: $screen-lg-min) {
            width: 320px;                       
        }
    }
}

