//// depracted, replaced by _cards.scss

.c-box {    
    background: #eee;
    overflow: hidden;
}

.c-box-pad {
    padding: $space;
}

.c-box-sk-white {
    @include border-bottom-brand-2();
    background: white;
}

/**
 * Content box
 */

@mixin c-box-flag {
    overflow: hidden;
}

@mixin c-box-stacked {
    padding: 0;
}

@mixin c-box-flag-image {
    padding: 0;
    margin: $space;    
    width: 50%;
    float: left;               
}

@mixin c-box-flag-content {
    padding: $space;  
    margin: 0;
    width: 100%;
}

@mixin c-box-stacked-image {    
    padding: 0;
    margin: 0;    
    width: 100%;
    float: none;
}

@mixin c-box-stacked-content {
    padding: $space;  
    margin: 0;
    width: 100%;
}

.c-box {        
    margin-bottom: 20px;
}

/* Flag */

.c-box-flag {
    @include c-box-flag;    

    .c-box-image {
        @include c-box-flag-image;
    }

    .c-box-content {          
        @include c-box-flag-content;
    }
}

.c-box-stacked {    
    @include c-box-stacked;        

    .c-box-image {
        @include c-box-stacked-image;
    }

    .c-box-content {          
        @include c-box-stacked-content;
    }
}

/* Stacked */

.c-box-flag-xs {
    @media (min-width: $screen-xs-min) { //above 480px        
        
        @include c-box-flag;   
    
        .c-box-image {
            @include c-box-flag-image;
        }

        .c-box-content {          
            @include c-box-flag-content;
        }
    }
}

