/**
 * Custom toolkit tabs - Using the BS tabs.js TEUXDEUX Make better mobile version
 */

.tk-tabs {
    padding: $space;
}


.tk-tabs-header { // tabs header with underline
    @extend .clearfix;
    border-bottom: 1px solid $color-border;
    a,
    button {
        @extend %all-caps;
        color: $color-font;
        padding: 10px 0;
    }
}

.tk-tabs-cta {
    @extend .clearfix;
    margin: $space-half 0;
}

.tk-tabs .tab-pane {
    padding: $space 0;
}

/* Tabs */

//Make tabs accessible, not diplay: none

.tab-content {
    position: relative;    

    > .tab-pane {
        @extend .hide-accessible;
        opacity: 0;
        display: block;     
    }    

    > .active {
        @extend .show-accessible;
        display: block;
        opacity: 1;
    }
}

//Change default styles

.nav-tabs > li > a,
.nav-pills > li > a {
    color: $color-font;    
    @extend .all-caps;
    
}

//toolkit nav tabs

.tk-nav-tabs {
    float: left;
    border: 0;    

    > li {
        margin-right: 15px;
        margin-bottom: 0;
    }

    > li > a,
    > li > a:hover,
    > li > a:focus {
        border: 0;
        padding: 10px 0;
        background: none;
        margin-bottom: -4px;
    }

    > li.active > a, 
    > li.active > a:hover,
    > li.active > a:focus {
        border: 0;
        border-bottom: 4px solid $color-brand-2;        
        background: none;
    }
}

.tk-nav-tabs-lg {
    border: 0;  
    border-bottom: 4px solid $color-brand-2;      

    .ie9 &,
    .lt-ie9 & {
        border-bottom: 1px solid $color-brand-2;
    }

    > li {        
        
    }

    > li > a,
    > li > a:hover,
    > li > a:focus {
        border: 0;
        margin: 0;
        padding: 10px;
        background: none;      
        text-align: center;
        background: $color-module;
        text-transform: uppercase;
        transition: background 0.25s ease;
    }

    > li.active > a, 
    > li.active > a:hover,
    > li.active > a:focus {
        border: 0;
        background: $color-brand-2;
        color: white;
    }

}



