/**
 * Sidebar
 */

.sidebar-heading {				
	padding: 5px 10px;
	margin: 0;
	margin-bottom: 0;
	border-bottom: 1px solid $color-border;	
	display: none;	

	@media (min-width: $screen-sm-min) {
		margin-bottom: 0;	
		padding: 5px 0px;
		display: block;
	}

	@media (min-width: $screen-md-min) {
		padding: 10px 0 10px 0;	
	}
	
}

.sidebar-button {	
	@extend %all-caps;
	display: block;
	width: 100%;
	background: $color-module;
	padding: 9px 30px 9px 30px;
	border: 0;		
	text-align: left;
	position: relative;			

	@media (min-width: $screen-xs - 1) {
		font-size: 15px;
		padding: 10px 5px 10px 40px;
	}

	@media (min-width: $screen-sm-min) {
		display: none;
	}

	&:before {
		@extend .tk-icon;	
		@extend .tk-icon-navicon:before;	
		color: $color-brand-2;
		position: absolute;
		top: 50%;
		left: 10px;
		margin-top: -9px;
		font-size: 16px;
		@media (min-width: $screen-xs-min) {
			left: 15px;
		}
	}

	&:after {
		@extend .tk-icon;	
		@extend .tk-icon-chevron-down:before;			
		position: absolute;
		top: 50%;
		right: 10px;
		margin-top: -9px;
		font-size: 16px;
		@media (min-width: $screen-xs-min) {
			right: 15px;
		}
	}

	.state-sidebar-active & {
		background: $color-brand-2;
		color: white;
		&:before {			
			@extend .tk-icon-close:before;				
			color: white;
		}
		&:after {						
			color: white;
		}
	}
}

.sidebar-body, //deprecated
.sidebar-container {
	display: none;	

	@media (min-width: $screen-sm-min) {
		display: block;		
	}
	.state-sidebar-active & {
		@media (max-width: $screen-md-max) {
			display: block;	
		}
	}
}

.sidebar-body-fw, //deprecated
.sidebar-container-fw { //full width sidebar body modifier
	.sidebar-heading {
		@extend %heading-related !optional;
		margin: 0;
		//@media (min-width: $screen-md-min) {     
			padding-left: $space-half;
		//}
	}
}

.sidebar-nav { //place on ul

	margin: 0 0 $space 0;
	padding: 0;			

	ul {
		display: none;
		padding: 0;
		margin: 0;			
		border-left: 2px solid $color-brand-2;
		background: rgba(#4d4843, 0.04);
	}

	li {
		list-style: none;				
	}

	a {
		font-size: 15px;
		padding: 6px 10px;
		display: block;		
		color: $color-link;
		color: $color-font;
		border-bottom: 1px $color-border solid;
    	transition: all 0.25s ease;

		@media (min-width: $screen-sm-min) {			
			padding: 5px;
			font-size: 16px;
		}	

		@media (min-width: $screen-md-min) {
			padding: 8px;
		}
		
		&:hover {
			text-decoration: none;
			background: $color-module;
		}	

		&:focus {
			text-decoration: none;
		}
	}


	> li > a { //top level
		
		padding: 6px 15px 6px 10px;		

		@media (min-width: $screen-sm-min) {
			padding: 5px 15px 5px 0;			
		}

		@media (min-width: $screen-md-min) {
			padding: 8px 0;
		}

		@media (min-width: $screen-lg-min) {
			padding: 8px 20px 8px 0;
		}

	}

	.dropdown > a {
		position: relative;		
		padding-right: 32px;

		&:after {
			@extend .tk-icon;	
			@extend .tk-icon-triangle-down:before;	
			right: 5px;
			font-size: 16px;
			position: absolute;
			top: 50%;
			margin-top: -8px;
			color: $color-brand-2;
		}
	}	

	.dropdown.open > ul {
		display: block;
	}

	.dropdown.open > a {
		position: relative;		
		padding-right: 26px;

		&:after {			
			@extend .tk-icon-triangle-up:before;				
		}
	}	

	.active > a {
		color: $color-brand-2;
	}
}

.no-js ul.sidebar-nav {
	.dropdown > ul {
		display: block;
	}
}

/**
 * Sidebar for full width page
 */

.sidebar-nav-fw { //sidebar modifier full width
	//@media (min-width: $screen-md-min) {     
		> li > a {
			padding-left: $space-half;
		}
	//}	
}

/**
 * Sidebar Actions
 */

%sidebar-list { //extendable style

	padding: 0;
	margin: 0 0 $space-xs 0;

	@media (min-width: $screen-sm-min) {		
		margin-bottom: $space-lg;
	}
	
	li {
		list-style: none;
		padding: 0;
		margin: 0;				
	}

	a {		
		padding: 6px 20px 6px 8px;
		display: block;		
		position: relative;
		font-size: 14px;		
		
		@media (min-width: $screen-sm-min) {			
			font-size: 15px;
		}	

		@media (min-width: $screen-md-min) {			
			padding: 8px 5px;
			font-size: 16px;
		}		

		@media (min-width: $screen-lg-min) {			
			padding: 8px 20px 8px 8px;
		}			

		&:hover {
			text-decoration: none;			
		}

		&:before {			
			@extend .tk-icon;	
			@extend .tk-icon-chevron-right:before;				
			position: absolute;
			top: 50%;
			margin-top: -8px;
			right: 6px;
			color: $color-brand-2;
		}
	}

	[class*="tk-icon"] {
		position: relative;
		top: 3px;
		margin-right: 6px;		
	}

}

.sidebar-university, //deprecated
.sidebar-contact, //deprecated
.sidebar-cta-list {	// plain list

	@extend %sidebar-list;

	a {
		border-bottom: 1px solid $color-border;
		color: $color-font;
	}

	[class*="tk-icon"] {
		color: $color-brand-2;
	}
}

.sidebar-cta, //depracted
.sidebar-cta-btn {	//gold boxes
	@extend %sidebar-list;

	a {
		@extend .all-caps;
		color: white;
		background: $color-brand-2;		
		border-bottom: 1px solid white;	

		@media (min-width: $screen-md-min) {
			font-size: 15px;
		}

		&:before {
			color: white;
		}			

		&:hover {			
			background: $color-brand-2-dark;
		}
	}
	
}

