.docs-hero {
	padding: 100px 50% 100px 0;
	position: relative;
	overflow: hidden;

	#canvas {
		position: absolute;
		top: 0;
		left: 0;
		width:: 100%;
		//height: 100%;
	}
}

.docs-hero-content {
	position: relative;
	z-index: 2;

	h1 {
		font-family: $font-family-big;
		font-weight: bold;
		border-bottom: 1px solid $color-brand-2;
		padding-bottom: 10px;
		margin-bottom: 10px;
		display: inline-block;
	}

}

.doc-block {
	//display: table;
	display: flex; 
	width: 100%;
}


.doc-block-img {
	width: 50%;
	//display: table-cell;
	background: #eee;
	//vertical-align: top;
}

.doc-block-content {
	padding: 50px 5%;
	width: 50%;
	background: $color-module-light;
	//display: table-cell;
	//vertical-align: top;

	h2 {
		@extend .heading-underline;
		font-size: 40px;
		font-weight: bold;
	}

	h4 {
		@extend %heading-related;
		text-align: center;
	}

	p {
		text-align: center;
		@extend .note;
	}

	.more {
		text-align: center;
		display: inline-block;
	}

}