/* BANNER SWIPER ALT - HERO */

/* COMMON SWIPER STYLES*/

.swiper-wrapper {
	height:	auto !important;    
}

.swiper-slide {	
	height:	auto !important;
    width: 100%;    
}

.swiper-slide-inner {
	display: table;	
	width: 100%;
}

//////

.no-js .swiper-wrapper {
    display: block;
}

.swiper-pagination {
    margin-bottom: 10px;
    margin-top: 10px;
}

.swiper-pagination-bullet {
    width: 10px;
    height: 10px;
    margin-right: 5px;    
    transition: background-color 500ms ease-out;
}

.swiper-pagination-bullet-active {    
    background: $color-brand-2;
}

/**
 * Swiper Banner
 */

.swiper-banner-alt,
.swiper-banner-sm {    
    position: relative;
    width: 100%;       
    margin: 0;
    background: $color-masthead;    

    @media (min-width: $screen-xs-min) {
        margin: 0;   
    }

    .swiper-container {
        width: 100%;   
        max-height: 580px;
        @media (min-width: $screen-md-min) {
            max-height: 420px;
        }
    }

    .swiper-wrapper {
        display: table;      
    }
    
        .swiper-slide {                
            display: table;
            overflow: hidden;
            position: relative; //for more link                                                            
        }

            .swiper-slide-active,
            .swiper-slide:nth-child(1) {
                height: auto !important;
              
            }

                .swiper-caption { //part with content
                    background: $color-masthead;
                    color: #fff;            
                    display: table-footer-group;        
                
                    @media (min-width: $screen-md-min) {
                        width: 33.33%;                    
                        display: table-cell;                                                                  
                        vertical-align: bottom;    
                    }  

                    .swiper-caption-cta {
                        display: block;    
                        padding: 0 15px;  
                        display: none;
                        @media (min-width: $screen-sm-min) {
                                                                            
                        }  
                        @media (min-width: $screen-md-min) {
                            padding: 0px 20px;       
                            display: block;                                                                
                        }                

                        a {                                                                      
                            display: none;
                            display: block;                  
                            font-size: 14px; 
                            padding: 10px 0;
                            color: white;
                            text-transform: uppercase;
                            border-top: 1px solid $color-brand-2; 
                                                  
                            @media (min-width: $screen-sm-min) {
                                font-size: 18px;
                            }     

                            &:after {      
                                @extend .tk-icon; 
                                @extend .tk-icon-chevron-right:before;     
                                color: $color-brand-2;                                
                                position: relative;
                                top: 2px;
                                left: 4px;                            
                            }
                        }                              
                    }

                    .swiper-caption-content {
                        display: block;    
                        padding: 10px;  

                        @media (min-width: $screen-sm-min) {
                            padding: 15px;                                                                       
                        }

                        @media (min-width: $screen-md-min) {
                            padding: 20px;                                                                       
                        }
                    }

                    h2 {
                        margin: 0 0 10px 0;
                        font-size: 18px;            
                        color: white;                                                       

                        @media (min-width: $screen-sm-min) {
                            font-size: 24px;     
                            font-family: $font-family-big;                         
                        }

                        @media (min-width: $screen-md-min) {
                            font-size: 32px;
                            margin: 0 0 10px 0;
                        }
                    }

                    p {
                        margin: 0;
                        font-size: 14px;

                        @media (min-width: $screen-sm-min) {
                            font-size: 16px;    
                            font-family: $font-family-big;                         
                        }

                        @media (min-width: $screen-md-min) {
                            font-size: 24px;
                            line-height: 36px;
                        }
                    }                   
                }

                .swiper-slide-bg { //part with image   
                    width: 100%;
                    padding-bottom: 50%;
                    background-size: 100%;                        
                    background-size: cover;    
                    display: table-cell;    
                    background-color: $color-module-dark;                            

                    img {
                        display: none;                        
                    }

                    @media (min-width: $screen-md-min) {                              
                        padding-bottom: 33.333%;                    
                        display: table-cell;
                        width: 66.666%;
                    }                    
                }


        .swiper-footer {}
                
            .swiper-tabs {
                @extend .clearfix;
                list-style: none;
                margin: 0;
                padding: 0;                        
                width: 100%;

                li {
                    float: left;
                    padding: 0px;
                    background: $color-masthead-light;
                    text-align: center;                
                    transition: background-color 500ms ease-out;                    
                    //border-top: 5px solid $color-masthead;
                
                    &.active {
                        //border-top: 5px solid $color-brand-2;                     
                        background: $color-brand-2;
                    }

                    a {
                        color: #fff;
                        text-decoration: none;
                        text-transform: uppercase;
                        width: 100%;
                        height: 100%;
                        width: 100%;
                        display: block;
                        text-align: center;
                        padding: 15px 15px 15px 15px;                       

                        @media (max-width: $screen-sm-max) {                              
                            padding: 0;
                            height: 10px;
                            text-indent: -9999px;
                        } 

                        @media (max-width: $screen-xs-max) {                                                         
                            height: 5px;                           
                        } 
                    }
                }
            }

            .swiper-tabs-2 li {
                width: 50%;
            }

            .swiper-tabs-3 li {
                width: 33.33%;   
            }

            .swiper-tabs-4 li {
                width: 25%;
            }

        .swiper-button {
            left: auto;
            right: auto;
            position: absolute;
			z-index: 1;
            top: 10px;            
            width: 30px;
            height: 30px;                 
             
            background-color: $color-masthead;                          
            background-position: center;
            background-repeat: no-repeat; 
            background-size: 50%;             

            @media (min-width: $screen-xs-min) {
                top: 10px;            
                width: 40px;
                height: 40px;       
            }  

            @media (min-width: $screen-md-min) {
                 
            }

            @media (min-width: ($screen-lg-min+80)) {       
                background-size: auto;     
                top: 10px;              
                width: 50px;
                height: 50px;
                top: 50%;
                margin-top: -25px;
                display: block;
            } 

            &.disabled {
            	pointer-events: none;
            	opacity: 0.5;
            	cursor: default;
            }
        }

        .swiper-button-prev {            
            right: 50px;     
            background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABoAAAAaCAYAAACpSkzOAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAAHlJREFUeNpiYKAQ/P//XwCIGxhoDYCWnP8PAetpacl8qCXvgdiAVpYU/EeAAFpZEoBkSQGtLDGABhUIzKeVJaAUdh9qyXl6pDCQZQKjKWw0hZEFmBgGA6BL0NE1MdA1edM1w9K1CKJroTqaEod8SlxPa4uIakACBBgAdqdYm+r89zIAAAAASUVORK5CYII=');            

            @media (min-width: $screen-xs-min) {
                right: 60px;    
            }  

            @media (min-width: ($screen-lg-min+80)) {                       
                right: auto;
                left: -45px;
                border-right: 5px solid $color-brand-2;
            }

            .svg & {
                background-image: url('data:image/svg+xml;base64,');            
            }
    
        }

        .swiper-button-next {            
            right: 10px;            
            background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABoAAAAaCAYAAACpSkzOAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAAHpJREFUeNpiYCAA/v//3wDEAgy0BEAL1v+HgPO0tsgAiN9DLZtPa8sC/iNAAa0tK0CyLIDWls2HWgQKSgNaW3Yeatl9mqZEkOFQS0ZT4mhKJD8lMjEMBUCXoKNLYqBL8qZLhqVbEUSXQnU0hQ3OKoGezS0BajUgAQIMAPXvW6glH+toAAAAAElFTkSuQmCC');

            @media (min-width: ($screen-lg-min+80)) {                 
                left: auto;
                right: -45px;
                border-left: 5px solid $color-brand-2;
            }        

            .svg & {
                background-image: url('data:image/svg+xml;base64,');            
            }                       
        }

        .swiper-pagination {
            width: 100%;
            margin-top: 10px;
            position: relative;
            display: none;
        }    
}



.lt-ie9 .swiper-banner-alt,
.lt-ie9 .swiper-banner-sm {    

    .swiper-slide-bg { //part with image                        
        position: relative;             

        img {
            display: block;            
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;            
        }
    }
}


.lt-ie9 .swiper-banner-alt,
.lt-ie9 .swiper-banner-sm {    

    .swiper-slide-bg { //part with image                        
        position: relative;             

        img {
            display: block;            
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;            
        }
    }
}


@keyframes pulseTop {
    0%   {top: 0px;}
    50%  {top: 6px;}
    100% {top: 0px;}
}

.swiper-more { // scroll down more
    padding: 5px;
    position: relative;
    -webkit-animation: pulseTop 2s;
    animation: pulseTop 2s;
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
    display: block;
    width: 16px;
    height: 16px;    
    margin: 5px auto;
    text-indent: -9999px;
    background: url('data:image/png;base64,') center no-repeat;

    @media (min-width: ($screen-sm-min)) {
        width: 26px;
        height: 26px;    
    }

    @media (max-width: $screen-sm-min) {
        display: none;
    }

    .svg & {
        background: url('data:image/svg+xml;base64,') center no-repeat;
    }
}


/**
 * Swiper Multiple items (see Accommodation: Single residence page)
 */
.swiper-multi {
    padding: 0 25px;
    background: #626267;
    overflow: initial;
    .owl-item {
        padding: 0 25px;
        text-align: center;
        p {
            max-width: 265px;
            margin: 0 auto;
            font-size: 16px;
            color: #000;
            margin-top: 10px;
            padding: 0px 10px 20px 10px;
        }
    }
    .owl-buttons {
        div {
            height: 100%;
            top: 25px;
        }
        .owl-prev {
            left: 0;
            top: 0;
            @media (min-width: 1340px) {
                top: 25px;
            }
        }
        .owl-next {
            right: 0;
            top: 0;
            @media (min-width: 1340px) {
                top: 25px;
            }
        }
    }
}