/**
* Pagination - Customising the BT pagination
*/

.pagination {
  
    > li {
        display: inline-block;                
    } 

    > li > a,
    > li > span {
        display: block;
        float: none;
        margin: 0;
        text-align: center;
        padding: $space-half $space;          
    }  

    > li.next > a,
    > li.next > span,
    > li.prev > a,
    > li.prev > span {
        padding: $space-half $space-sm;          
    }

       [class*="tk-icon"] {
        position: relative;
        top: 4px;
        width: 10px;
    } 
    
}
