/**
 * Images
 */


img {
	max-width: 100%; // never bigger than the container
}

//see courses for .round-img

.img-profile-round, //deprecated
.img-avatar { //used specifically for profiles
	border-radius: 1000px;
	width: 80px;
	height: 80px;	
	overflow: hidden;
	box-shadow: 0 1px 1px rgba(0, 0, 0, 0.25);
	padding: 0 !important; //when used in combination with rs-img

 	@media(min-width: $screen-sm-min){
		width: 100px;
		height: 100px;
 	}	
}

.img-avatar-lg {
	width: 120px;
	height: 120px;
	overflow: hidden;
 	@media(min-width: $screen-sm-min){
		width: 140px;
		height: 140px;
 	}
}

.img-avatar-sm {
	width: 60px;
	height: 60px; 	
	overflow: hidden;
}

.featured-img, //deprecated
.img-featured {	
	margin-bottom: $space;	
	width: 100%;
}

.jadu-cms,
.cms {

 	img {
        max-width: 100%;
        width: 100%;                      
        margin: 0 auto $space-half auto;
        display: block;
        height: auto;
    }

    img.float_right,
    img.alignright,
    img.align-right {
		width: auto;
		float: right;
		margin: 0 0 $space-half $space-half;
    }

    img.float_left,
    img.alignleft,
    img.align-left  {
		width: auto;
		float: left;
		margin: 0 $space-half $space-half 0;
    }   

    .img-accreditation {
	    max-width: 100%;
	    width: 100%;
	    margin-bottom: $space;
	    border: 1px solid $color-border;
	    padding: $space-half;
	    margin: 0;
	    display: block;
	    height: auto;
	    max-width: 40%;
	}

}

.embed-responsive {
	max-width: 100%;
}

/**
 * Responsive Images
 */

@mixin responsive-img-setup($namespace: ""){

	.rs-img#{$namespace} { //default square 1:1
		display: block;
		overflow: hidden;
		position: relative;
		padding-bottom: 100%;
		background-size: cover;	
		background-position: center center;
		background-repeat: no-repeat;		
		background-color: $color-brand-2;
		background-image: url('data:image/png;base64,');		

		a {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: rgba(0,0,0,0);
			transition: background .25s ease;

			&:hover {
				background: rgba(0,0,0,.25);
				
			}
		}

		img {
			position: absolute;
		    top: -9999px;
		    left: -9999px;	
		}
	}

	.rs-img-1-1#{$namespace} { //1:1
		background-size: cover;		
		padding-bottom: 100%;	
		background-image: url('data:image/png;base64,');	
	}

	.rs-img-4-3#{$namespace} { //4:3
		background-size: cover;		
		padding-bottom: 75%;	
		background-image: url('data:image/png;base64,');	
	}

	.rs-img-16-9#{$namespace} { //16:9
		background-size: cover;		
		padding-bottom: 56.25%;		
		background-image: url('data:image/png;base64,');
	}

	.rs-img-2-1#{$namespace} { //2:1
		background-size: cover;		
		padding-bottom: 50%;		
		background-image: url('data:image/png;base64,');
	}

	.lt-ie9 .rs-img#{$namespace} {
		background: none !important;
		position: relative;
		img {
			position: static;
			top: auto;
			left: auto;
		}
	}	    
}

@include responsive-img-setup();

@media (min-width: $screen-xs-min) {
    @include responsive-img-setup("-xs");
}

@media (min-width: $screen-sm-min) {
    @include responsive-img-setup("-sm");
}

@media (min-width: $screen-md-min) {
    @include responsive-img-setup("-md");
}

@media (min-width: $screen-lg-min) {
    @include responsive-img-setup("-lg");
}


