/**
 * Customisation of the Owl swiper
 */

/**
 * Default Swiper

+-----------------------+
|                       |
|                       |
|                       |
|                       |
+-----------------------+
| Content               |
+-----------------------+

*/

.swiper { //default

    background: $color-masthead;    
    opacity: 0;  /* opacity 0 until init */
    transition: opacity 0.1s;

    //Layout
    
    .slide {
        width: 100%;
    }

    .slide-inner {            
        display: table;
        width: 100%;
    }

    .slide-content {
        display: table-footer-group;        
        background: $color-masthead;
    }

    .slide-content-inner {     
        padding: $space;        
        @media (min-width: $screen-md-min) {        
            padding: $space-lg;        
        }            
    }

    .slide-img {
        width: 100%;
        padding-bottom: 50%;                
        display: table-cell;   
        background-size: cover;    
        background-position: center;                        
    }

    .slide-img img {
        display: none;
    }

    //Typography

    .slide-cta {
        color: white;
        text-transform: uppercase;        
        &:after {      
            @extend .tk-icon; 
            @extend .tk-icon-chevron-right:before;     
            color: $color-brand-2;                                
            position: relative;
            top: 2px;
            left: 4px;                            
        }           
    }

    .slide-heading {                
        color: white;          
        font-size: 20px;  
        margin-bottom: $space-half;

        @media (min-width: $screen-sm-min) {                                          
            font-size: 26px;
            font-family: $font-family-big;   
        }                                                                           
        
        @media (min-width: $screen-md-min) {                    
            font-size: 32px;            
        }                                                     
    }

    .slide-lead {        
        color: white;  
        margin-bottom: $space-half;
        font-size: 16px;
        @media (min-width: $screen-sm-min) {                                        
            font-size: 18px;            
        }     
        @media (min-width: $screen-md-min) {        
            margin-bottom: $space;
            font-family: $font-family-big;    
            font-size: 22px;      
        } 

        br {
            display: none;
            @media (min-width: $screen-md-min) {
                display: block;
            }
        }
    }
}

/**
 * Swiper 1

+----------+---------------+
|          |               |
|          |               |
|          |               |
| Content  |               |
+----------+---------------+

*/

.swiper-1 {

    @media (min-width: $screen-md-min) {

        //Layout

        .slide-content {
            display: table-cell;  
            width: 33.333333%;
            vertical-align: bottom;                 
        }

        .slide-img {
            display: table-cell;            
            width: 66.666666%;
            padding-bottom: 33.333333%;            
        }

        //Typography
    
        .slide-cta {        
            padding: $space 0 0 0;  
            display: block;                  
            font-size: 14px;                 
            border-top: 1px solid $color-brand-2;                                                                                    
            display: block;                                                                                        
            font-size: 18px;                                              
        }   

        .slide-heading {            
            margin: 0 0 10px 0;            
        }

        .slide-lead {        
            
            font-size: 14px;        
            font-size: 22px;
            line-height: 36px;
            
        } 
    }
}


/**
 * Swiper 2

+-----------------------+
|                       |
|                       |
|        Content        |
|                       |
|                       |
+-----------------------+

*/

.swiper-2 {

    @media (min-width: $screen-md-min) {

        // Layout

        .slide-inner {
            position: relative;                 

            &:after {
                content: "";                
                position: absolute;
                width: 100%;
                height: 100%;
                background: linear-gradient(to bottom, rgba(33,33,33,0) 0%, rgba(33,33,33,0.95) 100%);
                top: 0;
                left: 0;
                z-index: 0;
            }

        }

        .slide-content {                            
            padding: 5% 25%;
            background: none;     
            z-index: 1;
            text-align: center;                
            position: absolute;            
            bottom: 0;
            left: 0;         
            width: 100%;
        }            

        // Typography  

        .slide-cta {
            border: 1px solid white;        
            padding: 10px;
            background: rgba(33,33,33,0.7);
            padding: 12px 48px;

            &:after {
                display: none;
            }
        }

        .slide-heading {                
            text-shadow: 1px 1px 1px rgba(0,0,0,0.65);
            font-size: 56px; 
            position: relative;
            margin-bottom: 30px;
            display: inline-block;

            &:before {
                content: "";
                position: absolute;
                width: 70%;
                height: 1px;
                bottom: -8px;
                left: 15%;
                background-color: $color-brand-2;               
            }

        }

        .slide-lead {
            font-size: 26px;  
            line-height: 120%;
            text-shadow: 1px 1px 1px rgba(0,0,0,0.65);  
            margin-bottom: 28px;    
        }

    }
               
}

.lt-ie9 .swiper-2 .slide-inner:after {    
    background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABAQMAAAAl21bKAAAAA1BMVEUhISFeAxSwAAAAAXRSTlOzEo46UAAAAApJREFUCB1jYAAAAAIAAc/INeUAAAAASUVORK5CYII=);
}

.swiper-2.swiper-full-width {   

    @media (max-width: $screen-md-max) {
        .slide-img { //works with parrallax effect            
            background-position: center!important;
        }
    }

    @media (min-width: $screen-lg-min) {      

        .slide-img { //works with parrallax effect
            background-position: 0 -20px;
        }

        .slide-img {            
            padding-bottom: 40%;    
        }

    }

}

/**
 * Swiper nav & buttons
 */

.swiper-nav-2 li {
    width: 50%;
}

.swiper-nav-3 li {
    width: 33.33%;   
}

.swiper-nav-4 li {
    width: 25%;
}

.swiper .owl-buttons {
  
}

.swiper .owl-buttons > div {
    
    text-indent: -9999px;
    left: auto;
    right: auto;
    position: absolute;
    z-index: 2;
    top: 5px;            
    width: 30px;
    height: 30px;                 
     
    background-color: $color-masthead;                          
    background-position: center;
    background-repeat: no-repeat; 
    background-size: 50%;             

    @media (min-width: $screen-xs-min) {
        top: 10px;            
        width: 40px;
        height: 40px;       
    }     

    @media (min-width: ($screen-lg-min+80)) {       
        background-size: auto;     
        top: 10px;              
        width: 50px;
        height: 50px;
        top: 50%;
        margin-top: -25px;
        display: block;
    } 

    &.disabled {
        pointer-events: none;
        opacity: 0.25;
        cursor: default;
    }

}

.swiper .owl-buttons .owl-prev {            
    right: 40px;     
    background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABoAAAAaCAYAAACpSkzOAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAAHlJREFUeNpiYKAQ/P//XwCIGxhoDYCWnP8PAetpacl8qCXvgdiAVpYU/EeAAFpZEoBkSQGtLDGABhUIzKeVJaAUdh9qyXl6pDCQZQKjKWw0hZEFmBgGA6BL0NE1MdA1edM1w9K1CKJroTqaEod8SlxPa4uIakACBBgAdqdYm+r89zIAAAAASUVORK5CYII=');                

    @media (min-width: $screen-xs-min) {
        right: 60px;    
    }  

    @media (min-width: ($screen-lg-min+80)) {                       
        right: auto;
        left: -45px;
        border-right: 5px solid $color-brand-2;
    }    

    .svg & {
        background-image: url('data:image/svg+xml;base64,');            
    }

}

.swiper-full-width .owl-buttons .owl-prev {
    @media (min-width: ($screen-lg-min+80)) {   
        left: 0px;
    }
}

.swiper .owl-buttons .owl-next {           
    right: 5px;            
    background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABoAAAAaCAYAAACpSkzOAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAAHpJREFUeNpiYCAA/v//3wDEAgy0BEAL1v+HgPO0tsgAiN9DLZtPa8sC/iNAAa0tK0CyLIDWls2HWgQKSgNaW3Yeatl9mqZEkOFQS0ZT4mhKJD8lMjEMBUCXoKNLYqBL8qZLhqVbEUSXQnU0hQ3OKoGezS0BajUgAQIMAPXvW6glH+toAAAAAElFTkSuQmCC');

    @media (min-width: $screen-xs-min) {
        right: 10px;    
    }  

    @media (min-width: ($screen-lg-min+80)) {                 
        left: auto;
        right: -45px;
        border-left: 5px solid $color-brand-2;
    }        

    .svg & {
        background-image: url('data:image/svg+xml;base64,');            
    }                       
}

.swiper-full-width .owl-buttons .owl-next {    
    @media (min-width: ($screen-lg-min+80)) {   
        right: 0px;      
    }
}

.swiper-nav {
    @extend .clearfix;
    list-style: none;
    margin: 0;
    padding: 0;                        
    width: 100%;

    li {
        float: left;
        padding: 0px;
        background: $color-masthead-light;
        text-align: center;                
        transition: background-color 500ms ease-out;                            
    
        &.active {            
            background: $color-brand-2-dark;
        }

        a {
            color: #fff;
            text-decoration: none;
            text-transform: uppercase;
            width: 100%;
            height: 100%;
            width: 100%;
            display: block;
            text-align: center;
            padding: 15px 15px 15px 15px;    
            //border-right: 1px solid $color-masthead;                   

            @media (max-width: $screen-sm-max) {                              
                padding: 0;
                height: 10px;
                text-indent: -9999px;
            } 

            @media (max-width: $screen-xs-max) {                                                         
                height: 5px;                           
            } 
        }
    }
}
