/** 
 * News and events widget ajax filter
 */

.filter-box {
    display: none;
    position: relative;    

    ul {
        background: white;
        padding: $space;    
        box-shadow: 0px 1px 2px rgba(black, 0.25);        
        margin: 0;
        position: absolute;
        width: 100%;
        top: 0px;
        left: 0;
        z-index: 1;

        &:after {
            content: "";
            display: block; /* reduce the damage in FF3.0 */
            position: absolute;
            top: -10px;
            right: 10px;
            width: 0;
            border-width: 0 10px 10px;
            border-style: solid;
            border-color: #fff transparent;
        }

        li {
            list-style: none;
        }

        li a {
            &:after {
                @extend .tk-icon;
                @extend .tk-icon-chevron-right:before;       
                margin-left: 5px;
                position: relative;
                top: 2px;           
                color: $color-brand-2;
            }
        }
    }
}

.filter-box.active {
    display: block;    
}