///////sidebar widgets wordpress
.sidebar-widget {
    h3 {
        @extend %heading-related;
    }
}

.ajax-loader {
    width: auto !important;
}

.cookie-compliance {
    padding: 6px;
    background: $color-masthead-light;
    p {
        font-size: 15px;
        color: white;
        display: inline;
        margin-right: 6px;
    }
    a.btn {
        padding: 6px;
        line-height: 100%;
        margin-right: 6px;
        border-radius: 4px;
    }
}

.collapsing {
    //customises the bs collapse transition
    @include tk-transition(height, $duration: 0.3s, $effect: zoom);
}

//SINGLE RESIDENCE
.responsive-tabs li.btn {
    box-shadow: none;
    border: none;
    padding: 0;
}

.responsive-tabs li.btn a {
    padding: 10px;
}

.responsive-tabs > li.active > a,
.responsive-tabs > li.active > a:hover,
.responsive-tabs > li.active > a:focus {
    border-bottom: none;
}


/*RESIDENCE MAP STYLES*/

.marker {
    cursor: pointer;
    width: auto !important;
}

.marker-toggle {
    position: absolute;
    bottom: 0;
    left: 10px;
    button {
        color: #fff;
        border: none;
        padding: 10px;
        margin-right: 5px;
        background: #666;
        &.active {
            background: #212121;
        }
    }
}

.close-infowindow {
    position: relative;
    right: 9px;
    top: 5px;
    cursor: pointer;
    color: #fff;
    z-index: 1;
    border: 0;
    text-align: right;
    width: 27px;
    float: right;
}

//CUSTOM STYLES FOR GOOGLE MAPS INFO WINDOWS

/* white background and box outline */

.gm-style > div:first-child > div + div > div:last-child > div {
    > div:first-child > div {
        /* we have to use !important because we are overwritng inline styles */
        background-color: transparent !important;
        box-shadow: none !important;
        width: auto !important;
        height: auto !important;
        //arrow color
        div > div {
            background-color: #212121 !important;
        }
    }
    /* close button */
    > div > div:last-child {
        margin-right: 5px;
        margin-top: 5px;
    }
    img {
        display: none;
    }
}

/* positioning of infowindow */
.gm-style-iw {
    top: 22px !important;
    left: 22px !important;
}

// * Map
.marker-text {
    background: #212121;
    color: #fff;
    position: relative;
    padding: 10px;
    h3 {
        margin-bottom: 5px;
    }
    p {
        font-size: 14px;
        margin-bottom: 0;
    }
}

//this is needed for positioning elements for the responsive tabs
.pseudo-hide {
//    position: absolute;
//    opacity: 0;
//    z-index: -100;
    display: none;
}

.p-relative{
    position: relative;
}

.standard-map {
    width: 100%;
        height: 300px;
    * {
        box-shadow: none !important;
    }
    @media (min-width: $screen-xs-min) {
        height: 350px;
    }
   	@media (min-width: $screen-sm-min) {
            height: 450px;
   	}
}

